.login--container {
  margin-top: 5rem;
  margin-bottom: 5rem;
  padding: 3rem 1rem;
}

@media only screen and (min-width:768px){
  .login--container {
    border: 1px solid rgb(74, 132, 139);
    border-radius: 5px;
    width: 40%;
    margin: 0 auto;
    margin-top: 5rem;
    margin-bottom: 5rem;
    padding: 3rem 2rem;
  }
}

.login--container form input,
.login--container form button {
  width: 100%;
  border: 1px solid rgb(74, 132, 139);
  border-radius: 2px;
  padding: 10px;
  margin-top: 1rem;
}
.password {
  position: relative;
}
.password .password--field {
  width: 100%;
  padding: 10px;
}
.password--visibility {
  position: absolute;
  top: 30px;
  right: 15px;
  cursor: pointer;
}

/*****************/
.check--password--strength {
  border: 1px solid rgb(74, 132, 139);
  margin-top: 10px;
  padding: 0.5rem;
}

.check--strenght {
  display: flex;
  align-items: center;
}

/*************/
#cart table tr:nth-child(2n + 2) {
  background-color: rgba(230, 228, 228, 0.904);
}
#cart table tr th {
  text-align: left;
}
#cart table tr td {
  text-align: left;
}
#cart table tr th {
  padding: 0.7rem 0;
}
#cart table tr:hover {
  background-color: rgb(210, 219, 223);
}

/***********/
.product--details {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 2rem;
}
.product--details .details--img,
.product--details .details {
  border: 1px solid #ccc;
  padding: 2rem 1rem;
}
.details--img img{
  width: 100%;
  min-height: 300px;
  max-height: 350px;
}

@media only screen and (max-width:768px){
  .userinfo--form{
    width: 100%;
  }
}
@media only screen and (max-width:580px){
  .details--img .splide{
    padding: 0rem;
  }
}
