@tailwind base;
@tailwind components;
@tailwind utilities;

@import url("https://fonts.googleapis.com/css2?family=Spartan:wght@100;200;300;400;500;600;700;800;900&display=swap");

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Spartan", sans-serif;
  /* font-family: "Poppins", sans-serif; */
}
.searchbar{
  width: 40%;
}
/* #header .logo {
  width: 70%;
} */

.hide--nav{
  display: none;
}

h1 {
  font-size: 50px;
  line-height: 64px;
  color: #2222;
}
h2 {
  font-size: 46px;
  line-height: 54px;
  color: #3b3939;
}

h4 {
  font-size: 20px;
  color: #520f0f22;
}

h6 {
  font-weight: 700;
  font-size: 12px;
}
.bx-cart {
  font-size: 1.5rem;
}

p {
  font-size: 16px;
  color: #465b52;
  margin: 15px 0 20px 0;
}

.section-p1 {
  padding: 40px 80px;
}

.section-m1 {
  margin: 40px 0;
}

button.normal {
  font-size: 14px;
  font-weight: 600;
  padding: 15px 30px;
  color: #855f5f;
  background-color: #fff;
  border-radius: 4px;
  cursor: pointer;
  border: none;
  outline: none;
  transition: 0.2s;
}

button.white {
  font-size: 13px;
  font-weight: 600;
  padding: 11px 18px;
  color: #fff;
  background-color: transparent;
  cursor: pointer;
  border: 1px solid #fff;
  outline: none;
  transition: 0.2s;
}

body {
  width: 100%;
}
#header {
  display: flex;
  align-items: center;
  justify-content: space-between;

  /* display: grid;
grid-template-columns: repeat(4,1fr);
align-items: center; */

  padding: 2px 40px;
  width: 100%;
  background: #f3f3f3;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.178);
  z-index: 999;
  position: sticky;
  top: 0;
  left: 0;
}

.hamburger {
  display: none;
}
#navbar {
  display: flex;
  align-items: center;
  justify-content: center;
}

#navbar li {
  list-style: none;
  padding: 0 20px;
  position: relative;
}
#navbar li a {
  text-decoration: none;
  color: #1a1a1a;
  transition: 0.3s ease;
}

#navbar li a:hover,
#navbar li a:active {
  color: #ff8829;
}

