/* home.html */

#hero {
  height:70vh;
  width: 100%;
  background-size: contain;
  background-position: top 42% right -40%;
  background-repeat: no-repeat;
  background: linear-gradient(to left, #00000096,#000000e5 ),url("./../public/banner/ecommerce-selling-online-online-sales-e-commerce.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  padding-top: 4rem;
}
.hero-girl img {
  width: 10%;
  margin-bottom: rem;
}
.hero-img {
  margin-left: 7rem;
}
#hero h4 {
  padding-bottom: 15px;
}

#hero h1 {
  color: #ff8829;
}
.hero-img h2 {
  color: #ff8829;
}
.hero-img h4 {
  color: #000000;
}

#hero button {
  background-color: #ff8829;
  color: #fff;
  border: 0;
  padding: 14px 80px 14px 65px;
  background-repeat: no-repeat;
  cursor: pointer;
}

#feature {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
}

#feature .fe-box {
  width: 250px;
  text-align: center;
  padding: 25px 15px;
  box-shadow: 20px 20px 34px rgba(0, 0, 0, 0.03);
  border: 1px solid #cce7d0;
  border-radius: 4px;
  margin: 15px 0;
}

#feature .fe-box:hover {
  box-shadow: 10px 10px 54px rgba(70, 62, 221, 0.1);
}

#feature .fe-box img {
  width: 100%;
  margin-bottom: 10px;
}

#feature .fe-box h6 {
  display: inline-block;
  padding: 9px 8px 6px 8px;
  line-height: 1;
  border-radius: 4px;
  color: #ff8829;
  background-color: #fddde4;
}

#feature .fe-box:nth-child(2) h6 {
  background-color: #cdebbc;
}

#feature .fe-box:nth-child(3) h6 {
  background-color: #edd4f8;
}

#feature .fe-box:nth-child(4) h6 {
  background-color: #d1e8f2;
}

#feature .fe-box:nth-child(5) h6 {
  background-color: #f6dbf6;
}

#feature .fe-box:nth-child(6) h6 {
  background-color: #fff2e5;
}

#product1,
#product3 {
  text-align: center;
}

#product1 .pro-container,
#product3 .pro-container {
  display: flex;
  /* align-items: center; */
  justify-content: space-between;
  padding-top: 20px;
  flex-wrap: wrap;
}

#product1 .pro-container .individual--product,
.similar--items{
  width: 100%;
  display: grid;
  gap: 1rem;
  grid-template-columns: repeat(5,1fr);
}

#product1 .pro,
#product3 .pro {
  /* width: 23%; */
  /* min-width: 250px; */
  padding: 10px 12px;
  border: 1px solid #cce7d0;
  border-radius: 25px;
  cursor: pointer;
  box-shadow: 20px 20px 30px rgba(0, 0, 0, 0.02);
  margin: 15px 0;
  transition: 0.2s ease;
  position: relative;
}
#product1 .pro:hover,
#product3 .pro:hover {
  box-shadow: 20px 20px 30px rgba(0, 0, 0, 0.06);
  /* transform: scale(0.98); */
}

#product1 .pro img,
#product3 .pro img {
  width: 100%;
  border-radius: 20px;
  max-height: 190px;
  height: 190px;
}

#product1 .pro .des,
#product3 .pro .des {
  text-align: start;
  padding: 10px 0;
}

#product1 .pro .des span,
#product3 .pro .des span {
  color: #606063;
  font-size: 12px;
}

#product1 .pro .des h5,
#product3 .pro .des h5 {
  padding-top: 7px;
  color: #1a1a1a;
  font-size: 14px;
}

#product3 .pro .des h6 {
  padding-top: 7px;
  color: #1a1a1a;
  font-size: 14px;
}

#product1 .pro .des i,
#product3 .pro .des i {
  font-size: 12px;
  color: #ff8829;
}

#product1 .pro .des h4,
#product3 .pro .des h4 {
  padding-top: 7px;
  font-size: 15px;
  font-weight: 700;
  color: #ff8829;
}

#product1 .pro .cart,
#product3 .pro .cart {
  width: 40px;
  height: 40px;
  line-height: 40px;
  border-radius: 50px;
  background-color: #e8f6ea;
  font-weight: 500;
  color: #ff8829;
  border: 1px solid #cce7d0;
  position: absolute;
  bottom: 20px;
  right: 10px;
}

#banner {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  background-image: linear-gradient(#000000ad,#000000d2),url("./../public/banner/bnr.jpg");
  width: 100%;
  height: 40vh;
  background-size: cover;
  background-position: center;
}

#banner h4 {
  color: #fff;
  font-size: 16px;
}

#banner h2 {
  color: #fff;
  font-size: 30px;
  padding: 10px 0;
}

#banner h2 span {
  color: #ef3636;
}

#banner button:hover {
  background-color: #ff8829;
  color: #fff;
}

#sm-banner {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

#sm-banner .banner-box {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  text-align: center;
  /* background-image: url("banner/beautiful-beauty-black-friday-blur.jpg"); */
  min-width: 580px;
  height: 50vh;
  background-size: cover;
  background-position: center;
  padding: 30px;
}

#sm-banner .banner-box2 {
  /* background-image: url("banner/hanger-clothing-department-store.jpg");   */
}

#sm-banner h4 {
  color: #fff;
  font-size: 20px;
  font-weight: 300;
}

#sm-banner h2 {
  color: #fff;
  font-size: 28px;
  font-weight: 800;
}

#sm-banner span {
  color: #fff;
  font-size: 14px;
  font-weight: 500;
  padding-bottom: 15px;
}

#sm-banner .banner-box:hover button {
  background: #ff8829;
  border: 1px solid #ff8829;
}

#banner3 {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  padding: 0 80px;
}

#banner3 .banner-box {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  /* background-image: url("banner/male-guy-man-youth.jpg"); */
  min-width: 30%px;
  height: 30vh;
  background-size: cover;
  background-position: center;
  padding: 20px;
  margin-bottom: 20px;
}

#banner3 .banner-box2 {
  /* background-image: url("banner/FB_IMG_1473191511110.jpg"); */
}

#banner3 .banner-box3 {
  /* background-image: url("banner/supermarket-ecommerce-expense-market.jpg"); */
}

#banner3 h2 {
  color: #fff;
  font-weight: 900;
  font-size: 22px;
}

#banner3 h3 {
  color: #ec544e;
  font-weight: 800;
  font-size: 15px;
}

#newsletter {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  align-items: center;
  /* background-image: url(banner/business-city-communication-connection.jpg); */
  background-repeat: no-repeat;
  background-position: 20% 30%;
  background-color: #041e42;
  /* margin-left: 7px;
    margin-top: 10px; */
}

#newsletter h4 {
  font-size: 22px;
  font-weight: 700;
  color: #fff;
}

#newsletter p {
  font-size: 14px;
  font-weight: 600;
  color: #818ea0;
}

#newsletter p span {
  color: #ff8829;
}

#newsletter .form {
  display: flex;
  width: 40%;
  margin-right: 7px;
}

#newsletter input {
  height: 3.125rem;
  padding: 0 1.25em;
  font-size: 14px;
  width: 100%;
  border: 1px solid transparent;
  border-radius: 4px;
  outline: none;
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
}

#newsletter button {
  background-color: #ff8829;
  color: #fff;
  white-space: nowrap;
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
}
#newsletter .newstext {
  margin: 1rem 2rem;
}

/*Footer here*/
footer ul li {
  list-style: none;
  padding-top: 5px;
}
footer {
  background-color: #f3f3f3;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

footer .col {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin-bottom: 20px;
}

footer .col h4 {
  color: #000;
}

footer .logo {
  margin-bottom: 30px;
}

footer h4 {
  font-size: 14px;
  padding-bottom: 20px;
}

footer p {
  font-size: 13px;
  margin: 0 0 8px 0;
}

footer a {
  font-size: 13px;
  text-decoration: none;
  color: #222;
  margin-bottom: 10px;
}

footer .follow {
  margin-top: 20px;
}

footer .follow i {
  color: #465b52;
  padding-right: 4px;
  cursor: pointer;
}

footer .intall .row img {
  border: 1px solid #ff8829;
  border-radius: 6px;
}

footer .intall img {
  margin: 10px 0 15px 0;
}

footer .follow i:hover,
footer a:hover {
  color: #ff8829;
}

footer .copyright {
  width: 100%;
  text-align: center;
}

/*shop page*/
#page-header {
  background-image: linear-gradient(#0000001c,#000000ee),url("./../public/banner/ecommerce-selling-online-online-sales-e-commerce.jpg");
  width: 100%;
  height: 40vh;
  background-size: cover;
  display: flex;
  justify-content: center;
  text-align: center;
  flex-direction: column;
  padding: 14px;
}

#page-header h2,
#page-header p {
  color: #fff;
}

#pagination {
  text-align: center;
}

#pagination a {
  text-decoration: none;
  background-color: #ff8829;
  padding: 15px 20px;
  border-radius: 4px;
  color: #fff;
  font-weight: 600;
}

#pagination a i {
  font-size: 16px;
  font-weight: 600;
}

#pagination li {
  list-style: none;
  padding: o 20px;
  position: relative;
}

#pagination li a {
  text-decoration: none;
  font-size: 16px;
  transition: 0.3s ease;
}

#pagination li a:hover,
#pagination li a:active {
  color: #ff8829;
}

#pagination li a.active::after,
#pagination li a.hover::after {
  content: "";
}

/* single product */

#prodetails {
  display: flex;
  margin-top: 20px;
}

#prodetails .single-pro-image {
  width: 25rem;
  margin-right: 50px;
  border-radius: 20px;
}

.small-img-group {
  display: flex;
  justify-content: space-between;
}

.small-img-col {
  flex-basis: 24%;
  cursor: pointer;
}

#prodetails .single-pro-details {
  width: 50%;
  padding-top: 30px;
}

#prodetails .single-pro-details h4 {
  padding: 40px 0 20px 0;
}

#prodetails .single-pro-details h2 {
  font-size: 26px;
}

#prodetails .single-pro-details select {
  display: block;
  padding: 5px 10px;
  margin-bottom: 10px;
}

#prodetails .single-pro-details input {
  width: 50px;
  height: 47px;
  padding-left: 10px;
  font-size: 16px;
  margin-right: 10px;
}

#prodetails .single-pro-details input:focus {
  outline: none;
}
#prodetails .single-pro-details button {
  background: #ff8829;
  color: #fff;
}

#prodetails .single-pro-details span {
  line-height: 25px;
}

/* Blog page */

#blog-page-header {
  background-image: url("../public//banner/blog.jpg");
  width: 100%;
  height: 40vh;
  background-size: cover;
  display: flex;
  justify-content: center;
  text-align: center;
  flex-direction: column;
  padding: 14px;
}

#blog-page-header h2,
#blog-page-header p {
  color: #fff;
}

#blog {
  padding: 150px 150px 0 150px;
}

#blog .blog-box {
  display: flex;
  align-items: center;
  width: 100%;
  position: relative;
  padding-bottom: 90px;
}

#blog .blog-img {
  width: 50%;
  margin-right: 40px;
}

#blog img {
  width: 100%;
  height: 300px;
  object-fit: cover;
}

#blog .blog-details {
  width: 50%;
}

#blog .blog-details a {
  text-decoration: none;
  font-size: 11px;
  color: #000;
  font-weight: 700;
  position: relative;
  transition: 0.3s;
}

#blog .blog-details a::after {
  content: "";
  width: 50px;
  height: 1px;
  background-color: #000;
  position: absolute;
  top: 4px;
  right: -60px;
}

#blog .blog-details a:hover {
  color: #ff8829;
}

#blog .blog-details a:hover::after {
  background-color: #ff8829;
}

#blog .blog-box h1 {
  position: absolute;
  top: -40px;
  left: 0;
  font-size: 70px;
  font-weight: 700;
  color: #c9cbce;
  z-index: -9;
}

/* About Page */

#about-page-header {
  background-image: linear-gradient(#0000009d,#000000a4),url("./../public/banner/b4.jpg");
  width: 100%;
  height: 40vh;
  background-size: cover;
  display: flex;
  justify-content: center;
  text-align: center;
  flex-direction: column;
  padding: 14px;
}

#about-page-header h2,
#about-page-header p {
  color: #fff;
}

#about-head {
  display: flex;
  align-items: center;
}

#about-head img {
  width: 50%;
  height: auto;
}

#about-head div {
  padding-left: 40px;
}

#about-head div h2 {
  color: #1a1a1a;
}

#about-head div marquee {
  background-color: #ff8829;
  font-size: 50px;
  height: 10vh;
}

#about-app {
  text-align: center;
  background-color: #ff8829;
}

#about-app h1 {
  color: #1a1a1a;
}

#about-app .video {
  width: 70%;
  height: 1000%;
  margin: 30px auto 0 auto;
}

#about-app .video video {
  width: 25%;
  height: 25%;
  border-radius: 20px;
}

/* contact Page */

#contact-details {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

#contact-details .details {
  width: 40%;
}

#contact-details .details span,
#form-details form span {
  font-size: 12px;
}

#contact-details .details h2,
#form-details form h2 {
  font-size: 26px;
  line-height: 35px;
  padding: 20px 0;
}

#contact-details .details h3 {
  font-size: 16px;
  padding-bottom: 15px;
}

#contact-details .details li {
  list-style: none;
  display: flex;
  padding: 10px 0;
}

#contact-details .details li i {
  font-size: 14px;
  padding-right: 22px;
}

#contact-details .details li p {
  margin: 0;
  font-size: 14px;
}

#contact-details .map {
  width: 55%;
  height: 400px;
}

#contact-details .map iframe {
  width: 100%;
  height: 100%;
}

#form-details {
  display: flex;
  justify-content: space-between;
  margin: 30px;
  padding: 80px;
  border: 1px solid #e1e1e1;
}

#form-details form {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

#form-details form input,
#form-details form textarea {
  width: 100%;
  padding: 12px 15px;
  outline: none;
  margin-bottom: 20px;
  border: 1px solid #e1e1e1;
}

#form-details form button {
  background-color: #ff8829;
  color: #fff;
}

#form-details .people div {
  padding-bottom: 25px;
  display: flex;
  align-items: flex-start;
}

#form-details .people div img {
  width: 65px;
  height: 65px;
  object-fit: cover;
  margin-right: 15px;
}

#form-details .people div p {
  margin: 0;
  font-size: 13px;
  line-height: 25px;
}

#form-details .people div p span {
  display: block;
  font-size: 16px;
  font-weight: 600;
  color: #000;
}

/* Cart Page */

#cart-page-header h2,
#cart-page-header p {
  color: #fff;
}

#cart table {
  width: 100%;
  border-collapse: collapse;
  table-layout: fixed;
  white-space: nowrap;
}
.active--orders--table td,
.active--orders--table th,
.order--details table td,
.order--details table th{
white-space: nowrap;
}

#cart table img {
  width: 70px;
}

#cart table thead {
  border: 1px solid #e2e9e1;
  border-left: none;
  border-right: none;
}

#cart table thead td {
  font-weight: 700;
  text-transform: uppercase;
  font-size: 13px;
  padding: 18px 0;
}

#cart table tbody tr td {
  padding: 10px 0;
}

#cart table tbody td {
  font-size: 13px;
}

#cart-add {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

#coupon {
  width: 50%;
  margin-bottom: 30px;
}

#coupon h3,
#subtotal h3 {
  padding-bottom: 15px;
}

#coupon input {
  padding: 10px 20px;
  outline: none;
  width: 60%;
  margin-right: 10px;
  border: 1px solid #e2e9e1;
}

#coupon button,
#subtotal button {
  background-color: #ff8829;
  color: #fff;
  padding: 12px 20px;
}

#subtotal {
  width: 50%;
  margin-bottom: 30px;
  border: 1px solid #e2e9e1;
  padding: 30px;
}

#subtotal table,
.active--orders--table,
.order--details table {
  border-collapse: collapse;
  width: 100%;
  margin-bottom: 20px;
}

#subtotal table td {
  width: 50%;
  border: 1px solid #e2e9e1;
  padding: 10px;
  font-size: 13px;
}
#header .nav-nav-elements li{
  padding:  .5rem ;
