.personal--information--sidebar,
.dashboard--main {
  display: flex;
  /* gap: 1rem; */
  justify-content: space-between;
  width: 100%;
  height: 100vh;
}

.pagination {
  display: flex;
  align-items: center;
  gap: 0.3rem;
  justify-content: center;
}
.page-num {
  border: 1px solid gray;
  padding: 6px 10px;
  border-radius: 3px;
  text-align: center;
}
.page-num:hover {
  background-color: #088178;
}
.activePage {
  background-color: #088178;
}
.sidebar {
  /* background-color: #088178; */
  background-color: #02161d;
  width: 22%;
  overflow-y: scroll;
  padding: 2rem 1rem;
}
.sidebar ul li {
  list-style: none;
  padding-top: 5px;
  display: flex;
}

.sidebar ul li a:hover {
  color: gray;
}
.sidebar ul li a {
  color: white;
  text-decoration: none;
  transition: all 0.3s ease;
  padding: 5px;
  display: flex;
  align-items: center;
  gap: 10px;
}

.personal--information {
  padding: 2rem 1rem;
  width: 78%;
  overflow-y: scroll;
  /* background-color: rgb(235, 241, 243); */
  background-color: rgb(255, 255, 255);
}

/******************/
.userinfo--img {
  width: 150px;
  height: 150px;
  border-radius: 50%;
  border: 4px solid white;
}
.userinfo--form input {
  width: 50%;
  padding: 10px;
  border-radius: 5px;
  border: 1px solid gray;
  margin-top: 10px;
  margin-bottom: 10px;
}

.dashboard--info .dashboard--stats {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 0.7rem;
  align-items: center;
  cursor: pointer;
}
.dashboard--stats .product--stats:hover {
  transform: translateY(-5px);
}
.dashboard--stats .product--stats {
  padding: 0.5rem 2rem;
  display: flex;
  gap: 1rem;
  align-items: center;
  transition: all 0.3s ease-out;
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.267);
}
.dashboard--stats h5 {
  font-weight: bold;
  color: white;
}
.dashboard--stats h3 {
  font-size: 200%;
  font-weight: 700;
  color: white;
}
.dashboard--table tr {
  border-top: 1px solid gray;
  border-bottom: 1px solid gray;
  text-align: left;
  transition: all 0.3s ease-out;
}
.dashboard--table tr:nth-child(2n + 2),
.active--orders--table tr:nth-child(2n + 2),
.order--details table tr:nth-child(2n + 2) {
  background-color: rgba(40, 124, 124, 0.247);
}
.dashboard--table tr:hover,
.active--orders--table tr:hover,
.order--details table tr:hover {
  background-color: rgb(75, 180, 221);
}
.dashboard--table .table--head,
.active--orders--table thead,
.order--details table thead {
  border-top: 3px solid #088178;
  border-bottom: 3px solid #088178;
}
.active--orders--table tbody img {
  width: 100px;
  height: 80px;
}
.active--orders--table thead tr {
  text-align: left;
}
.dashboard--table td,
.dashboard--table th,
.active--orders--table td,
.active--orders--table th,
.order--details table td,
.order--details table th {
  padding: 0.8rem;
  white-space: nowrap;
}
.dashboard--table th,
.active--orders--table th {
  padding: 0.5rem;
}
.dashboard--table table,
.active--orders--table {
  width: 100%;
  /* border: 1px solid red; */
}
.dashboard--table,
.active--orders--table,
.order--details table {
  margin-top: 1rem;
}

.add--product form input,
.add--product form select {
  width: 70%;
  margin-top: 1rem;
  padding: 10px;
  border: 1px solid gray;
}
.add--product form textarea {
  width: 70%;
  border: 1px solid gray;
  padding: 10px;
  margin-top: 1rem;
}
.add--product .gallery--images img {
  width: 180px;
  /* width: 100%; */
  height: 130px;
  cursor: pointer;
  border: 2px solid #555;
}
.add--product .gallery--images input[type="file"] {
  /* visibility: hidden; */
  display: none;
}
.add--product .gallery--images .images--field {
  /* display: block; */
  display: flex;
  margin-top: 2rem;
  gap: 1rem;
}
.sidebar--items li.sidebar--item--active {
  background-color: red;
}


/*address book styles*/
.address--details form{
  width: 100%;
}
.address--details form input, 
.address--details form select{
  border: 1px solid gray;
  padding: 10px;
  width: 47%;
  margin-bottom: 2rem;
  border-radius: 5px;
}
.name--fields,
.phone--fields,
.location--fields{
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.address--details form #address{
  width: 100%;
}
/*address book styles*/

@media only screen and (max-width: 768px) {
  .userinfo--form input {
    width: 100%;
  }
  .dashboard--table,
  .active--orders--table,
  .order--details table {
    overflow-x: scroll;
  }
  .add--product form input,
  .add--product form select,
  .add--product form textarea {
    width: 100%;
  }
  .sidebar {
    width: 0%;
    padding: 0;
    transition: all 0.3s ease-out;
  }
  .side--active{
    width: 120%;
    padding: .5rem 1rem;
  }
  .personal--information {
    width: 100%;
  }
}

@media only screen and (max-width: 580px) {
  .sidebar--tab {
    /* display: none; */
  }


  aside{
    /* display: none; */
  }

  .user--profile {
    width: 100%;
    border-width: 2px;
  }
  .dashboard--info .dashboard--stats {
    /* display: block;
    margin-bottom: 1rem; */
    overflow-x: scroll;
  }
  .dashboard--info .dashboard--stats {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 0.7rem;
    align-items: center;
    cursor: pointer;
  }
  .dashboard--stats .product--stats {
    padding: 1rem 3rem;
  }

  .dashboard--stats .stats {
    margin-bottom: 1rem;
  }
  .inventory--items {
    flex-direction: column;
    gap: 1rem;
    margin: 3rem 0;
  }
  .thumbnail {
    width: 50%;
  }
  .order--details img {
    width: 260px;
    max-width: 100px;
  }
}
@media only screen and (min-width: 580px) {
  .power--btn {
    display: none;
  }
}
.active {
  /* background-color: rgba(35, 202, 188, 0.863); */
  color:#088178;
  padding: 0.1rem 0.5rem;
  border-radius: 2px;
  width: 100%;
}
